.game-window {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    justify-content: center;
    align-items: center;
    animation: showWindow 1s;
    user-select: none;
}

.word-container {
    display: flex;
    gap: .5rem;
}

.figute-keypad-container {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
    align-items: center;
}

.figure-container {
    width: 40%;
    height: 50%;
}

.notifications-and-keypad-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 4px 6px 0px #0e0e0e;
}

.notifications-container {
    width: 100%;
    text-align: center;
    font-size: 2rem;

}

.keypad-container {
    display: flex;
    flex-wrap: wrap;
    gap: .6rem;
    justify-content: center;
    background: gray;
    padding: 1rem;
}

@media screen and (max-width:425px){
    .game-window {
        gap: 2rem;
    }

    .figure-container {
        width: 40%;
        height: 30%;
        height: auto;
    }

    .notifications-and-keypad-container {
        width: 100%;
    }
  
    
  }